.navbar {
  width: 100vw;
  background: #7a8c5c;
  padding: 0;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(46,125,50,0.07);
}
.menu {
  display: flex;
  width: 100vw;
  height: 64px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu li {
  flex: 1 1 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.menu li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.08rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  padding: 0 8px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: color 0.2s, transform 0.2s, background 0.2s;
}
.menu li a::after {
  content: "";
  display: block;
  height: 2.5px;
  width: 0;
  background: #fff;
  border-radius: 2px;
  transition: width 0.3s cubic-bezier(.4,0,.2,1);
  position: absolute;
  left: 0;
  bottom: 12px;
}
.menu li a:hover {
  color: #c8f7c5;
  background: #6b7b4b;
  transform: scale(1.06);
}
.menu li a:hover::after {
  width: 100%;
  background: #c8f7c5;
}
@media (max-width: 900px) {
  .navbar, .menu {
    height: auto;
    flex-direction: column;
    width: 100vw;
  }
  .menu li {
    width: 100vw;
    justify-content: flex-start;
  }
  .menu li a {
    font-size: 1rem;
    justify-content: flex-start;
    padding-left: 16px;
    height: 48px;
  }
} 